import React from 'react';
import Avatar from '@material-ui/core/Avatar';
import Button from '@material-ui/core/Button';
import CssBaseline from '@material-ui/core/CssBaseline';
import TextField from '@material-ui/core/TextField';
import FormControlLabel from '@material-ui/core/FormControlLabel';
import Checkbox from '@material-ui/core/Checkbox';
import Link from '@material-ui/core/Link';
import Grid from '@material-ui/core/Grid';
import Box from '@material-ui/core/Box';
import LockOutlinedIcon from '@material-ui/icons/LockOutlined';
import Typography from '@material-ui/core/Typography';
import { makeStyles } from '@material-ui/core/styles';
import Container from '@material-ui/core/Container';
import Paper from "@material-ui/core/Paper";
import WrentLogo from './wrentLogo';


const useStyles = makeStyles((theme) => ({
      paper: {
        marginTop: theme.spacing(8),
        display: 'flex',
        flexDirection: 'column',
        alignItems: 'center',
        padding: theme.spacing(5),
        marginLeft: -theme.spacing(3),
        width: '450px',
      },
    avatar: {
        margin: theme.spacing(1),
        backgroundColor: theme.palette.secondary.main,
    },
    form: {
        width: '100%', // Fix IE 11 issue.
        marginTop: theme.spacing(3),
    },
    submit: {
        margin: theme.spacing(3, 0, 2),
    },
}));

export default function SignUp() {
    const classes = useStyles();

    var firstName = "";
    var lastName = "";
    var userName = "";
    var email = "";
    var location = "";
    var password = "";

    const handleFirstNameChange = (event) => {
        firstName = event.target.value;
    }

    const handleLastNameChange = (event) => {
        lastName = event.target.value;
    }

    const handleUserNameChange = (event) => {
        userName = event.target.value;
    }

    const handleEmailChange = (event) => {
        email = event.target.value;
    }

    const handleLocationChange = (event) => {
        location = event.target.value;
    }

    const handlePasswordChange = (event) => {
        password = event.target.value;
    }

    const handleSubmit = (event) => {
        event.preventDefault();
        if (!firstName || !lastName || !userName || !email || !location || !password) {
            alert('One of the required fields is empty');
        } else {
            postUser();
        }
    }

    const postUser = async () => {
        await fetch('/users', {
            method: 'POST',
            headers: {
              'Content-type': 'application/json'
            },
            body: JSON.stringify({
                'name': firstName.toString() + " " + lastName.toString(),
                'password': password.toString(),
                'email': email.toString(),
                'location': 1,
                'userName': userName.toString(),
                'permission': 1
            })
        })
    }

    return (
        <Container component="main" maxWidth="xs">
            <CssBaseline />
            <Paper className={classes.paper}>
                <WrentLogo />
                <Typography component="h1" variant="h5">
                    Sign up
        </Typography>
                <form className={classes.form} noValidate>
                    <Grid container spacing={2}>
                        <Grid item xs={12} sm={6}>
                            <TextField onChange={handleFirstNameChange}
                                autoComplete="fname"
                                name="firstName"
                                variant="outlined"
                                required
                                fullWidth
                                id="firstName"
                                label="First Name"
                                autoFocus
                            />
                        </Grid>
                        <Grid item xs={12} sm={6}>
                            <TextField onChange={handleLastNameChange}
                                variant="outlined"
                                required
                                fullWidth
                                id="lastName"
                                label="Last Name"
                                name="lastName"
                                autoComplete="lname"
                            />
                        </Grid>
                        <Grid item xs={12}>
                            <TextField onChange={handleUserNameChange}
                                variant="outlined"
                                required
                                fullWidth
                                id="userName"
                                label="User Name"
                                name="userName"
                                autoComplete="user name"
                            />
                        </Grid>
                        <Grid item xs={12}>
                            <TextField onChange={handleEmailChange}
                                variant="outlined"
                                required
                                fullWidth
                                id="email"
                                label="Email Address"
                                name="email"
                                autoComplete="email"
                            />
                        </Grid>
                        <Grid item xs={12}>
                            <TextField onChange={handleLocationChange}
                                variant="outlined"
                                required
                                fullWidth
                                id="location"
                                label="Location"
                                name="location"
                                autoComplete="location"
                            />
                        </Grid>
                        <Grid item xs={12}>
                            <TextField onChange={handlePasswordChange}
                                variant="outlined"
                                required
                                fullWidth
                                name="password"
                                label="Password"
                                type="password"
                                id="password"
                                autoComplete="current-password"
                            />
                        </Grid>
                        <Grid item xs={12}>
                            <FormControlLabel
                                control={<Checkbox value="allowExtraEmails" color="primary" />}
                                label="I want to receive inspiration, marketing promotions and updates via email."
                            />
                        </Grid>
                    </Grid>
                    <Button onClick={handleSubmit}
                        type="submit"
                        fullWidth
                        variant="contained"
                        color="primary"
                        className={classes.submit}
                    >
                        Sign Up
          </Button>
                    <Grid container justify="flex-end">
                        <Grid item>
                            <Link href="/login" variant="body2">
                                Already have an account? Sign in
              </Link>
                        </Grid>
                    </Grid>
                </form>
            </Paper>
        </Container>
    );
}